import Gif from "../Gif"
import	"./ListofGifs.css"

export default function ListOfGifs({gifs}){


    return (
    <div className="ListOfGifs">
    {gifs.map(({id,title,url,...restOfGif}) => 
    <Gif 
    key={id} 
    title={title}
    url={url} 
    id={id}
    extraInfo={restOfGif}
    />
    )}
    </div>
    )
}