import React, {useEffect, useRef,useCallback} from "react"
import Spinner from "components/Spinner"
import ListOfGifs from "components/ListOfGifs"
import {useGifs} from "hooks/useGifs"
import useNearScreen from "hooks/useNearScreen"
import debounce from "just-debounce-it"
export default function SearchResults({params}){
    const {keyword=""} = params;
    const {loading,gifs,setPage} = useGifs({keyword})
    const externalRef = useRef()
    const {isNearScreen,fromRef} = useNearScreen({externalRef:loading?null:externalRef})
    //const handleNextPage = () => setPage(prevpage=>prevpage+1)
    //const handleNextPage = () => console.log("next page")

    //console.log(isNearScreen)
    //guardar la funcion en referencia para que el programa no cree una funcion cada vez que se renderiza
    

    const debounceHandleNextPage = useCallback(debounce(
        ()=>setPage(prevpage=>prevpage+1),200),[])
    
    
    useEffect(()=>{
        console.log(isNearScreen)
        if(isNearScreen){
            debounceHandleNextPage()
        }
    },[isNearScreen,debounceHandleNextPage])

    return <>
    {loading?<Spinner />:
    <>
    <h3 className="App-title">Gifs de {decodeURI(keyword)}</h3>
    <ListOfGifs gifs={gifs}/>
     <div id="visor" ref={externalRef}></div>
    </>}
    {/*infinite scroll*/}
    </>
}





