import {API_KEY,API_URL} from "./settings"

const apikey = API_KEY
const fromApiResponseToGifs = (response)=>{
  const {data=[]} =response
      if(Array.isArray(data)){
        
        const gifs = data.map(image =>
          { 
          const {images,title,id} = image  
          const {url} =  images.downsized_medium
          return {url,title,id}
        })
        return gifs;
      }
      /*else{
       for single gif search*/
        /*const {images,title,id} = data
        const {url} =  images.downsized_medium
        return {url,title,id}
      }*/
      return [];
}





/* intentar hacerla con async await  */
export async function getGifs({keyword = '', limit = 5, id="", page=0}={}){
  let apiURL = `${API_URL}gifs/search?api_key=${apikey}&q=${keyword}&limit=${limit}&offset=${page*limit}&rating=g&lang=en${id}`
    
  if(id!==""){
    apiURL = 'https://api.giphy.com/v1/gifs/'+id+'?api_key='+apikey
  }

  const res = await fetch(apiURL)
    const response = await res.json()
    const gifs = fromApiResponseToGifs(response)   
    return gifs;
}